import React from "react"
import { Bar } from "react-chartjs-2"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import axios from "axios"
import moment from "moment"

import "tabler-react/dist/Tabler.css";

import { Card, Page } from "tabler-react";

const chartColors = {
    red: 'rgb(255, 99, 132)',
    orange: 'rgb(255, 159, 64)',
    yellow: 'rgb(255, 205, 86)',
    green: 'rgb(75, 192, 192)',
    blue: 'rgb(54, 162, 235)',
    purple: 'rgb(153, 102, 255)',
    grey: 'rgb(201, 203, 207)'
};

const initialState = {
  loading: true,
  labels: [],
  datasets: [{
            label: "Influenza-like Illness Syndrome",
            data: [],
            type: 'line',
            pointRadius: 0,
            fill: false,
            lineTension: 0,
            borderWidth: 2,

            backgroundColor: chartColors.blue,
            borderColor: chartColors.blue,
        }, {
            label: "Hand Foot and Mouth Disease Syndrome",
            data: [],
            type: 'line',
            pointRadius: 0,
            fill: false,
            lineTension: 0,
            borderWidth: 2
        }, {
            label: "Acute Gastroenteritis Syndrome",
            data: [],
            type: 'line',
            pointRadius: 0,
            fill: false,
            lineTension: 0,
            borderWidth: 2,

            backgroundColor: chartColors.red,
            borderColor: chartColors.red,
        }, {
            label: "Acute Conjunctivitis Syndrome",
            data: [],
            type: 'line',
            pointRadius: 0,
            fill: false,
            lineTension: 0,
            borderWidth: 5,

            backgroundColor: chartColors.green,
            borderColor: chartColors.green,
        }]
};

const options = {
        responsive: true,
        maintainAspectRatio: false,
        title: {
            display: false
        },
        scales: {
            xAxes: [{
                type: 'time',
                distribution: 'series',
                time: {
                    parser: (dateString) => moment(dateString, 'DD-MMM-YYYY', 'en')
                }
            }],
            yAxes: [{
                scaleLabel: {
                    display: true,
                    labelString: 'Weekly Average Rate'
                }
            }]
        }
    };
  
class AEDSurveillance extends React.Component {

	componentWillMount() {
		this.setState();
	}
	componentDidMount() {
    this.fetchAEDSurveillance();
	}
  
  fetchAEDSurveillance() {
    
    var _this = this;
    
      axios
        .get(`https://ourhealthcarehk.github.io/api/AEDSURVEILLANCE.json`)
        .then(({ data }) => {
          
          if (!data)
            return;          
          
          var state = Object.assign({}, initialState);  // copy the initial state, careful not to modify it.
          state.loading = false;          
          state.labels = [];
          state.datasets[0].data = [];
          state.datasets[1].data = [];
          state.datasets[2].data = [];
          state.datasets[3].data = [];
          
          for (var i = 2; i < data.length; i++) {
            state.labels.push(data[i][3]);
            state.datasets[0].data.push(data[i][4]);
            state.datasets[1].data.push(data[i][5]);
            state.datasets[2].data.push(data[i][6]);
            state.datasets[3].data.push(data[i][7]);
          }
          console.log(state);

          _this.setState(state)
        })
        .catch(error => {
          _this.setState({ loading: false, error })
        })
    }
    
	render() {
  return (
  <Layout>
    <SEO title="急症傳染病症狀監測" />
    <Page.Content title="急症傳染病症狀監測">
      <p>以急診室求診的主要病徵為依據，追蹤市內各種傳染病症的流行程度。</p>
      <Card>
          <Card.Body>
			      <Bar data={this.state} options={ options } height="600" width="600"/>
          </Card.Body>
        </Card>
    </Page.Content>
  </Layout>
  );
  }
}

export default AEDSurveillance
